'use client';

import Image from 'next/image';
import Link from 'next/link';
import { useEffect, useMemo, useRef, useState } from 'react';
import { motion, useMotionValue, animate } from 'framer-motion';

type Slide = {
  id: string | number;
  imageSrc: string;
  imageAlt?: string;
  title?: string;
  blurb?: string;
  ctaLabel?: string;
  ctaHref?: string;
};

type Props = {
  slides: Slide[];
  autoPlayMs?: number;     // set to 0 to disable
};

export default function HeroCarousel({ slides, autoPlayMs = 7000 }: Props) {
  const [index, setIndex] = useState(0);
  const count = slides.length;
  const containerRef = useRef<HTMLDivElement>(null);
  const width = useContainerWidth(containerRef);
  const x = useMotionValue(0);

  // snap to current index whenever it changes
  useEffect(() => {
    const target = -index * width;
    const controls = animate(x, target, { type: 'spring', stiffness: 300, damping: 35 });
    return controls.stop;
  }, [index, width, x]);

  // autoplay
  useEffect(() => {
    if (!autoPlayMs) return;
    const t = setInterval(() => setIndex((i) => (i + 1) % count), autoPlayMs);
    return () => clearInterval(t);
  }, [count, autoPlayMs]);

  const goTo = (i: number) => setIndex((i + count) % count);
  const prev = () => goTo(index - 1);
  const next = () => goTo(index + 1);

  // Decide next/prev based on drag distance + velocity
  const onDragEnd = (_: any, info: { offset: { x: number }; velocity: { x: number } }) => {
    const swipe = info.offset.x + info.velocity.x * 0.35; // swipe power
    if (swipe > 100) prev();
    else if (swipe < -100) next();
    else goTo(index); // snap back
  };

  return (
    <section className="w-full select-none">
      <div ref={containerRef} className="relative w-full overflow-hidden bg-black">
        {/* Track (each slide fills container) */}
        <motion.div
          className="flex"
          style={{ x }}
          drag="x"
          dragElastic={0.06}
          dragConstraints={{ left: -width * (count - 1), right: 0 }}
          onDragEnd={onDragEnd}
          aria-roledescription="carousel"
        >
          {slides.map((s, i) => (
            <div key={s.id} className="w-full shrink-0">
              {/* IMAGE */}
              <div className="relative aspect-[16/9] w-full md:aspect-[21/9] lg:aspect-[24/9]">
                <Image
                  src={s.imageSrc}
                  alt={s.imageAlt ?? ''}
                  fill
                  priority={i === index}
                  sizes="100vw"
                  className="object-cover"
                />
              </div>

              {/* TEXT + CTA (below image) */}
              <div className="flex flex-col items-center bg-white py-10 text-center">
                {s.title && (
                  <h2 className="px-6 text-3xl font-extrabold uppercase tracking-tight md:text-5xl">
                    {s.title}
                  </h2>
                )}
                {s.blurb && (
                  <p className="mt-3 max-w-2xl px-6 text-neutral-700 md:text-lg">
                    {s.blurb}
                  </p>
                )}
                {s.ctaHref && s.ctaLabel && (
                  <Link
                    href={s.ctaHref}
                    className="mt-6 inline-block rounded-full bg-black px-6 py-3 text-white transition hover:bg-neutral-800"
                  >
                    {s.ctaLabel}
                  </Link>
                )}
              </div>
            </div>
          ))}
        </motion.div>

        {/* ARROWS */}
        <div className="pointer-events-none absolute bottom-4 right-4 z-10 flex gap-2 md:bottom-6 md:right-6">
          <button
            aria-label="Previous slide"
            onClick={prev}
            className="pointer-events-auto grid h-10 w-10 place-items-center rounded-full bg-white/90 shadow hover:bg-white"
          >
            ‹
          </button>
          <button
            aria-label="Next slide"
            onClick={next}
            className="pointer-events-auto grid h-10 w-10 place-items-center rounded-full bg-white/90 shadow hover:bg-white"
          >
            ›
          </button>
        </div>
      </div>

      {/* BULLETS (below text, static) */}
      <div className="flex items-center justify-center gap-3 bg-white py-6">
        {slides.map((s, i) => (
          <button
            key={s.id}
            onClick={() => goTo(i)}
            aria-label={`Go to slide ${i + 1}`}
            className={`h-2.5 rounded-full transition-all ${
              i === index ? 'w-8 bg-neutral-900' : 'w-2.5 bg-neutral-300 hover:bg-neutral-500'
            }`}
          />
        ))}
      </div>
    </section>
  );
}

/* -------- helpers -------- */

function useContainerWidth(ref: React.RefObject<HTMLElement>) {
  const [w, setW] = useState(0);
  useEffect(() => {
    const el = ref.current;
    if (!el) return;
    const ro = new ResizeObserver(() => setW(el.clientWidth));
    ro.observe(el);
    setW(el.clientWidth);
    return () => ro.disconnect();
  }, [ref]);
  return w;
}
